.header-wrapper {
  background-color: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 375px) {
  .mob-header {
    width: 375px;
  }
}

.header__logo {
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding-top: 18px;
  padding-bottom: 18px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__logo {
    margin-right: 40px;
  }
}

@media screen and (min-width: 1440px) {
  .header__logo {
    margin-right: 76px;
    padding-top: 22px;
    padding-bottom: 22px;
  }
}

.menu {
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: #111;
  align-items: center;
  gap: 24px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: none;
}

@media screen and (min-width: 768px) {
  .menu {
    display: flex;
  }
}

.menu.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.menu__shopping {
  align-items: center;
  gap: 4px;
  display: flex;
}

.icon-cart {
  fill: currentColor;
}

.current {
  color: #111;
  background: #eac645;
  border-radius: 36px;
  padding: 8px 16px;
  font-weight: 700;
}

.menu-toggle {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.icon-burger {
  fill: #000;
}

.menu-toggle :hover, .menu-toggle :focus {
  fill: #4f2ee8;
}

.header__group {
  justify-content: center;
  align-items: center;
  gap: 14px;
  margin-left: auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__group {
    gap: 24px;
  }
}

.auth__modal-open {
  width: 184px;
  height: 45px;
  color: #fff;
  background-color: #4f2ee8;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: center;
  align-items: center;
  gap: 90px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: none;
}

@media screen and (min-width: 768px) {
  .auth__modal-open {
    display: flex;
  }
}

.auth__modal-open.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  display: none;
}

.auth__modal-open-js:hover, .auth__modal-open-js:focus {
  box-shadow: 0 8px 8px rgba(0, 0, 0, .3);
}

.authorised-btns__wrapper {
  position: relative;
}

.authorised-btns__wrapper.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  display: none;
}

.btn-user {
  letter-spacing: -.02em;
  color: #fff;
  height: 45px;
  background-color: #4f2ee8;
  border: 1px solid #4f2ee8;
  border-radius: 36px;
  justify-content: space-around;
  align-items: center;
  gap: 5px;
  font-size: 22px;
  font-weight: 700;
  line-height: 29px;
  display: none;
}

@media screen and (min-width: 768px) {
  .btn-user {
    display: flex;
  }
}

.fone-icon-user {
  width: 37px;
  height: 37px;
  background-color: rgba(17, 17, 17, .6);
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.icon-user {
  fill: #4f2ee8;
}

.icon-triangle-down {
  fill: #fff;
}

.btn-user:hover, .btn-user:focus {
  box-shadow: 0 8px 8px rgba(0, 0, 0, .3);
}

.log-out-btn {
  width: 184px;
  height: 48px;
  color: #111;
  background-color: #fff;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: center;
  align-items: center;
  gap: 90px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
