  .log-out-btn {
    display: none;
  }
}

.log-out-btn.is-hidden, .modalll.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modalll {
  width: 100%;
  height: 100vh;
  z-index: 7;
  background-image: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  padding: 20px;
  transition: opacity .15s linear, visibility .15s linear;
  position: absolute;
  top: 64px;
  left: 0;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .modalll {
    display: none;
  }
}

